<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div style=\&quot;margin-bottom: 16px;\&quot;&gt;\n        Tab&lt;v-select :data=\&quot;positionOptions\&quot; v-model=\&quot;position\&quot; size=\&quot;lg\&quot; style=\&quot;width: 200px;\&quot;&gt;&lt;/v-select&gt;    \n    &lt;/div&gt;\n    &lt;v-tabs active-tab-key=\&quot;1\&quot; :position=\&quot;position\&quot;&gt;\n        &lt;v-tab-pane tab-key=\&quot;1\&quot; tab=\&quot;\&quot;&gt;&lt;/v-tab-pane&gt;\n        &lt;v-tab-pane tab-key=\&quot;2\&quot; tab=\&quot;\&quot;&gt;&lt;/v-tab-pane&gt;\n        &lt;v-tab-pane tab-key=\&quot;3\&quot; tab=\&quot;\&quot;&gt;&lt;/v-tab-pane&gt;\n    &lt;/v-tabs&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data() {\n            return {\n                position: 'top',\n                positionOptions: [\n                    {label: 'top', value: 'top'},\n                    {label: 'bottom', value: 'bottom'},\n                    {label: 'left', value: 'left'},\n                    {label: 'right', value: 'right'}\n                ]\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
