        <span class="hljs-tag">&lt;<span class="hljs-name">v-tab-pane</span> <span class="hljs-attr">tab-key</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">tab</span>=<span class="hljs-string">""</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-tab-pane</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-tab-pane</span> <span class="hljs-attr">tab-key</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">tab</span>=<span class="hljs-string">""</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-tab-pane</span>&gt;</span>
