<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-tabs active-tab-key=\&quot;tab1\&quot; type=\&quot;editable-card\&quot; @add=\&quot;onAdd\&quot; @remove=\&quot;onRemove\&quot;&gt;\n        &lt;v-tab-pane v-for=\&quot;tabPane in tabPanes\&quot; :tab-key=\&quot;tabPane.key\&quot; :key=\&quot;tabPane.key\&quot; :tab=\&quot;tabPane.tab\&quot;&gt;{{ tabPane.content }}&lt;/v-tab-pane&gt;\n    &lt;/v-tabs&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data() {\n            return {\n                keyIndex: 1,\n                tabPanes: [\n                    {key: 'tab1', tab: '1', content: '1'},\n                    {key: 'tab2', tab: '2', content: '2'},\n                    {key: 'tab3', tab: '3', content: '3'}\n                ]\n            }\n        },\n        methods: {\n            onAdd() {\n                this.tabPanes.push({\n                    key: 'newTab' + this.keyIndex,\n                    tab: '' + this.keyIndex,\n                    content: '' + this.keyIndex++ + ''\n                });\n            },\n            onRemove(tabKey) {\n                for (let i = 0; i &lt; this.tabPanes.length; i++) {\n                    if (this.tabPanes[i].key == tabKey) {\n                        this.tabPanes.splice(i, 1);\n                        break;\n                    }\n                }\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-tabs active-tab-key="tab1" type="editable-card" @add="onAdd" @remove="onRemove">
        <v-tab-pane v-for="tabPane in tabPanes" :tab-key="tabPane.key" :key="tabPane.key" :tab="tabPane.tab">{{ tabPane.content }}</v-tab-pane>
    </v-tabs>
</template>


