  for (let i = 0; i &lt; this.customTabPanes.length; i++) {\n                    if (this.customTabPanes[i].key == tabKey) {\n                        this.customTabPanes.splice(i, 1);\n                        break;\n                    }\n                }\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div style="margin-bottom: 16px;">
        Tab<v-select :data="positionOptions" v-model="customPosition" size="lg" style="width: 200px;"></v-select>
        <v-button @click="onCustomAdd"></v-button>
    </div>
    <v-tabs :active-tab-key="customActiveKey" type="editable-card" :position="customPosition" hide-add="" @remove="onCustomRemove">
        <v-tab-pane v-for="tabPane in customTabPanes" :tab-key="tabPane.key" :key="tabPane.key" :tab="tabPane.tab">{{ tabPane.content }}</v-tab-pane>
    </v-tabs>
</template>


